<template>
  <div v-if="showBanner" class="cookie-banner">
    <p>This website uses cookies to enhance the user experience.</p>
    <button @click="acceptCookies">Accept</button>
    <button @click="declineCookies" class="decline">Decline</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showBanner: !this.$cookies.get("cookieAccepted") && !this.$cookies.get("cookieDeclined"),
    };
  },
  methods: {
    acceptCookies() {
      this.$cookies.set("cookieAccepted", true, "365d");
      this.showBanner = false;
    },
    declineCookies() {
      this.$cookies.set("cookieDeclined", true, "7d");
      this.showBanner = false;
    },
  },
};
</script>

<style scoped>
.cookie-banner {
  position: fixed;
  bottom: 10px;
  left: 10px;
  right: 10px;
  background: #333;
  color: white;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  z-index: 1000;
}
button {
  margin-left: 10px;
  padding: 5px 10px;
  background: #ff9800;
  color: white;
  border: none;
  cursor: pointer;
}
button:first-of-type {
  background: #ff9800;
  color: white;
}
button.decline {
  background: #888;
  color: white;
}
</style>
