<script>
import BurgerMenu from "@/components/BurgerMenu.vue";
export default {
  name: 'NavBar',
  props: {
    msg: String
  },
  components: {
    BurgerMenu
  },
  data() {
    return {
      isScrolled: false,
      oldLogo: require("@/assets/images/minicrinwhite.webp"),
      newLogo: require("@/assets/images/minicrinyell.webp"),
    }
  },
  methods: {
    handleScroll() {
      this.isScrolled = window.scrollY > 50;
    },
    scrollToTop() {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  },
}
</script>
