<template>
  <section>
    <div class="menu-restaurant">
      <NavBar />
      <div class="container">
        <div class="row padding-y150">
          <div class="col">
            <h2 class="menu-h2">Meniu Restaurant</h2>
          </div>
        </div>
      </div>
    </div>

    <!-- Food Categories -->
    <div class="menu-categories">
      <div class="container">
        <div class="row">
          <div 
            class="col-lg-3 col-md-4 col-sm-6" 
            v-for="(menu, index) in menus" 
            :key="index">
            <div class="card mb-4" @click="scrollTo(menu.anchor)">
              <img :src="menu.image" class="img-fluid card-img-top" alt="Card image">
              <div class="card-body">
                <p class="card-text text-center">{{ menu.text }}</p>
              </div>
            </div>
          </div>
        </div>

        <!-- Food Items Form -->
        <div class="row mt-5">
          <div v-for="menu in menus" :key="menu.id" :ref="menu.anchor" class="col-12 food-section">
            <h3 class="food-title text-center my-5">{{ menu.text }}</h3>
            <!-- List of added foods -->
            <div class="table-responsive">
              <table class="table table-bordered table-hover">
                <tbody>
                  <tr
                    v-for="(food, i) in menu.foodItems" :key="i"
                  >
                    <td class="card-name">{{ food.name }}</td>
                    <td>{{ food.weight }}</td>
                    <td>{{ food.price }} RON</td>
                  </tr>
                </tbody>
            </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    <BackTop/>
    <InfoPlace/>
    <Footer/>
  </section>
</template>

<script>
import { menus } from './data.js'
import InfoPlace from '@/components/InfoPlace.vue';
import NavBar from '@/components/NavBar.vue';
import BackTop from '@/components/BackTop.vue';
import Footer from '@/components/Footer.vue';

export default {
  components: {
    NavBar,
    InfoPlace,
    Footer,
    BackTop
  },
  data() {
    return {
      menus 
    }
  },
  methods: {
    scrollTo(anchor) {
      const section = this.$refs[anchor][0];
      if (section) {
        const offset = 90; // Adjust scroll position 30px above the section
        const topPosition = section.getBoundingClientRect().top + window.scrollY - offset;
        
        window.scrollTo({
          top: topPosition,
          behavior: "smooth"
        });
      }
    },
  }
};
</script>

