<template>
  <div :class="['burger', { scrolled: isScrolled }]">
    <!-- Burger Button -->
    <button class="burger-btn" :class="{ open: menuOpen, scrolled: isScrolled }" @click="toggleMenu">
      <span></span>
      <span></span>
    </button>

    <!-- Overlay (Dark background when open) -->
    <div v-if="menuOpen" class="overlay" @click="toggleMenu"></div>

    <!-- Sidebar Menu -->
    <nav :class="['sidebar', { open: menuOpen }]">
      <ul>
        <button class="burger-btn btn-sidebar" :class="{ open: menuOpen, scrolled: isScrolled }" @click="toggleMenu">
          <span></span>
          <span></span>
        </button>
        <li><router-link to="/" @click="scrollToTop">Acasă</router-link></li>
        <li><router-link to="/meniu-restaurant" @click="scrollToTop">Meniu Restaurant</router-link></li>
        <li><router-link to="/despre" @click="scrollToTop">Despre Noi</router-link></li>
        <li><router-link to="/contact" @click="scrollToTop">Contact</router-link></li>
        <li><img src="../assets/images/crinblack.webp" alt="Crin" width="50"/></li>
        <li class="social-nav">
          <a href="https://www.facebook.com/ienciu2012/" target="_blank">
            <i class="fa-brands fa-square-facebook"></i>
          </a>
          <a href="https://www.instagram.com/crinul_imperial" target="_blank">
            <i class="fa-brands fa-square-instagram"></i>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menuOpen: false,
      isScrolled: false
    };
  },
  methods: {
    toggleMenu() {
      this.menuOpen = !this.menuOpen;
    },
    scrollToTop() {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    },
    handleScroll() {
      this.isScrolled = window.scrollY > 50; // Change to black when scrolled more than 50px
    },
    handleResize() {
      if (window.innerWidth > 768) {
        this.menuOpen = false; // Close menu if screen is resized to larger than 768px
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    window.addEventListener("resize", this.handleResize);
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
    window.removeEventListener("resize", this.handleResize);
  }
};
</script>

